import { Disclosure } from '@headlessui/react'
import { ChevronUpIcon } from '@heroicons/react/20/solid'
import { v4 as uuidv4 } from 'uuid';

const list= [
  {question:"Lm th no  ng k cc kha hc ca trung tm?", answer:"Bn ch cn  li thng tin trong mc bn di hoc lin h trc tip hotline ca trung tm  c h tr t vn min ph."},
  {question:"Lch hc ca trung tm nh th no?", answer:"Trung tm c lch hc cc lp linh hot, hc vin s c kim tra xp lp ph hp vi trnh  v thi gian c nhn."},
  {question:"Trung tm c cp chng ch khng?", answer:"Ti NZEC, hc vin c o to pht trin 4 k nng t cn bn n nng cao   kh nng thi v t cc Chng ch Ting anh quc t c cp bi cc t chc uy tn."},
]
const bgImg= "https://st4.depositphotos.com/3206865/21452/i/450/depositphotos_214528520-stock-photo-triangle-blue-pink-light-green.jpg";


export default function FrequentlyAsked() {


  return (
    <div className="w-full px-4 pt-16 pb-20 h-screen bg-no-repeat bg-cover" style={{backgroundImage: `url(${bgImg})`}}>

        <h2 className='font-extrabold text-gray-700 mb-7 text-center '> Cc cu hi thng gp ca Trung Tm</h2>
        <div className="mx-auto w-full max-w-4xl rounded-xl bg-transparent  p-2  ">
        

        {list.map((item, index)=>{
          return(
            <Disclosure key={uuidv4()}>
            {({ open }) => (
              <>
                <Disclosure.Button className="flex w-full my-2 justify-between rounded-lg bg-purple-100 px-20 py-2 text-left font-bold text-xl text-purple-900 hover:bg-purple-200 focus:outline-none focus-visible:ring focus-visible:ring-purple-500/75">
                  <span>{item.question}</span>  
                  <ChevronUpIcon
                    className={`${
                      open ? 'rotate-90 transform transition-all duration-700' : ''
                    } h-5 w-5 text-purple-500`}
                  />
                </Disclosure.Button>
                <Disclosure.Panel className="px-20 pb-2 pt-4 text-xl text-gray-500 font-semibold">
                  {item.answer}
                </Disclosure.Panel>
              </>
            )}
          </Disclosure>
          )



        })}



        
      </div>
    </div>
  )
}
